import './css/index.css';

const cmntFace = ["[→_→]", "[呵呵]", "[嘻嘻]", "[哈哈]", "[爱你]", "[挖鼻屎]", "[吃惊]", "[晕]", "[泪]", "[馋嘴]", "[抓狂]", "[哼]", "[可爱]", "[怒]", "[汗]", "[害羞]", "[睡觉]", "[钱]", "[偷笑]", "[笑cry]", "[doge]", "[喵喵]", "[二哈]", "[酷]", "[衰]", "[闭嘴]", "[鄙视]", "[花心]", "[鼓掌]", "[悲伤]", "[思考]", "[生病]", "[亲亲]", "[怒骂]", "[太开心]", "[懒得理你]", "[右哼哼]", "[左哼哼]", "[嘘]", "[委屈]", "[吐]", "[可怜]", "[打哈气]", "[挤眼]", "[失望]", "[顶]", "[疑问]", "[困]", "[感冒]", "[拜拜]", "[黑线]", "[阴险]", "[互粉]", "[抱抱]", "[摊手]", "[心]", "[伤心]", "[猪头]", "[熊猫]", "[兔子]", "[握手]", "[作揖]", "[赞]", "[耶]", "[good]", "[弱]", "[不要]", "[ok]", "[haha]", "[来]", "[威武]", "[鲜花]", "[钟]", "[浮云]", "[飞机]", "[月亮]", "[太阳]", "[微风]", "[下雨]", "[给力]", "[神马]", "[围观]", "[话筒]", "[奥特曼]", "[草泥马]", "[萌]", "[囧]", "[织]", "[礼物]", "[喜]", "[围脖]", "[音乐]", "[绿丝带]", "[蛋糕]", "[蜡烛]", "[干杯]", "[男孩儿]", "[女孩儿]", "[肥皂]", "[照相机]", "[浪]", "[沙尘暴]"];

/**
 * 返回过滤表情后的字符串 和 减去的长度
 * 为了更精确的计算原始评论长度
 * @param {String} content 
 * @param {Number} limit  限制的长度（从0开始，不传默认为不限制）
 * @return {Object} 返回替换后的评论内容HTML字符串(全部替换)(returnValue.str)、替换后的评论内容HTML字符串(截取)(returnValue.strLimit)、替换掉的表情数量(returnValue.replaceNum)、替换掉的字符串长度(returnValue.replaceLen)、替换的字符串长度（returnValue.addLen： <span class='....'></span>）
 */
function replaceFace(content, limit) {
  var data = {
    str: '',
    strLimit: '',
    replaceNum: 0,
    replaceLen: 0,
    addLen: 0 // 替换后增加的距离。假设只有一个表情，一个表情在替换前默认算为1，替换后为变长，例如25。要求截取到第40位，则实际应截取到40-1+25位
  }
  if (!/\[\/?[^\]]*]/g.test(content)) {
    data.str = content;
    data.strLimit = content;
    if (limit) {
      data.strLimit = content.substring(0, limit + 1);
    }
    return data
  }
  if (limit == null) {
    limit = 9007199254740992;
  }
  let offsetMark = 0; // 标记需要截取的字段
  let lastLess = 0; // 上次进入替换函数时还缺少的字符串长度
  let firstOverflow = 0; // 当进入替换函数且超出字数限制时 只需再计算一次应截取的位置
  data.str = content.replace(/\[\/?[^\]]*]/g, function(item, offset, oriStr) {
    var curIndex = cmntFace.indexOf(item);
    if (curIndex < 0) {
      return item;
    }
    let outStr = '<span class="faceList_' + parseInt(curIndex / 28 + 1) + ' face icon_' + (curIndex % 28) + '"></span>';
    // 偏移 - 表情多计算的长度(data.replaceLen - data.replaceNum) < 限制
    if (offset - (data.replaceLen - data.replaceNum) <= limit) {
      lastLess = limit - (offset - (data.replaceLen - data.replaceNum));
      data.replaceNum = data.replaceNum + 1;
      data.replaceLen = data.replaceLen + item.length;
      data.addLen = data.addLen + outStr.length;
      // 原位置 + 前方的(新表情长度- 原表情长度)；  -data.replaceLen中已把本次的减去了 要再加上  
      offsetMark = offset + data.addLen - data.replaceLen + item.length;
    } else {
      if (!firstOverflow) {
        offsetMark = offsetMark + lastLess;
        firstOverflow = 1;
      }
    }
    return outStr
  });

  // 针对情况 例如：  
  // [表情]字字字字字字字字字 限制字数为4，最后一个匹配到的替换表情比这个数都小，所以需在外层再检测一次
  if (!firstOverflow) {
    offsetMark = offsetMark + lastLess;
    firstOverflow = 1;
  }

  if (offsetMark == 0) {
    offsetMark = limit + 1;
  }

  data.strLimit = data.str.substring(0, offsetMark);

  return data;
}

export default replaceFace;